import React from 'react'; import styled from 'styled-components'; 
// -------------------------------------------------------
import ImageGallery from 'react-image-gallery';
import images  from '~Client/Containers/Content/AboutMe/Components/Music/Photo';
const Music = () => (
    <Wrapper>
        <div style={{ background: 'rgba(26,0,13,.9)'}}> 
            <Header>Музыка</Header>
        </div>
        <ImageGallery styles={{height: '1000px'}} items={images}></ImageGallery>
        <TextWR>
            <Paragraph>Я и мои друзья любили тяжелый роЦкк :0 , и обычное слушание дошло до выступлений</Paragraph>
            <Paragraph>Но после парочки выступлений группа распалась, а найти новую не увенчалось успехом в моем городе Запорожье, найти человека который действительно умел бы играть редкость...</Paragraph>
            <Paragraph>Затем я переехал жить в Москву, и там у меня появились некоторые проблемы, которые оторвали меня от этой связи, Внутренее желание увенчалось</Paragraph>
            <Paragraph>Но я надеюсь что смогу заработать денег, чтоб сделать студию для себя, где я бы играл на инструментах :)</Paragraph>
            <Paragraph>Сейчас я слушаю музыку в диапазоне (Аккадемическая музыка - тяжелый электро дабстеп - минимал - техно - пситранс)</Paragraph>
        </TextWR>
    </Wrapper>
)
//  -----------------------------------------------------
const Wrapper = styled.div `width: 100%; display: flex; flex-direction: column; background: #000; margin: 10px 0;`
    const Header  = styled.h2  `padding: 4px 0 8px 0; text-align: center; font-size: 300%; color: #fff; `
// -- About music text
    const TextWR    = styled.div `width: 95%; margin: 0px auto 10px auto; display: inline-flex; flex-direction: column; justify-content: center; 
        padding: 15px 20px; text-align: left; border-radius: 50px; `
    const Paragraph = styled.p   `padding: 20px 70px 20px 10px; margin: 10px 0; box-shadow: inset 0 0 10px 0 #fff; text-shadow: 0 0 4px;
        font-size: 200%; color: #fff; border-bottom: 2px ridge #333; border-top: 2px ridge #333; border-radius: 20px;
            @media (max-width: 1100px) { font-size: 130% }
            @media (max-width: 720px)  { padding: 10px 5px; text-align: center; }
    &:nth-child(even) { text-align: right; padding-left: 70px; padding-right: 10px; 
        @media (max-width: 720px) { padding: 10px 5px; text-align: center; }}`
    
    export default Music