import React from 'react'; import styled from 'styled-components';
// ---------------------------------------------------------
import GameBackground from '~PREF/Img/Games.jpg';
const Game = () => (
    <Wrapper>
        <Bcg />
        <Header>Игры</Header>
        <FlexItem>
            <Paragraph>С малых лет я полюбил компьютерные игры, в 6 лет мне подарили SonyPlaystation, и я проводил с ней всё свое свободное время.</Paragraph>
            <Paragraph>Позже появился компьютер, и большую часть подросткового времени проводил именно за ним.</Paragraph>
            <Paragraph>Прошел много интересный и крутых игр, атмосферы их внутренего мира всегда впечатляли</Paragraph>
            <Paragraph>Так же меня интересовал кибер спорт, я играл в CSGO и смог получить самое большое звание в игре</Paragraph>
        </FlexItem>  
        <FlexItem style={{height: '40%'}}>
            <Paragraph>Хотел собрать свою комманду, или найти уже существующую для соревновательных игр с другими людьми</Paragraph>
            <Paragraph>Но жизнь не позволяла мне постоянно сидеть за компьютером и тренировать свои навыки игры</Paragraph>
            <Paragraph>Поэтому я перестал тратить на это много времени, и играл иногда в удовольствие :)</Paragraph>
        </FlexItem>
    </Wrapper>
)

const Wrapper = styled.div `width: 100%; max-height: 100vh; padding: 20px 40px; position: relative;
    border-top: 8px double rgba(255,255,255, .9); z-index: 1; margin: 10px 0;
    display: flex; justify-content: space-around; flex-direction: column;
    @media (max-width: 600px)  { padding: 55px 5px 5px 5px; }`
const Bcg     = styled.div `width: 100%; height: 100%; position: absolute; top: 0; left: 0%; z-index: -1; border-radius: 10px;   
    background: center center url(${GameBackground}); `
    // ------------------------------------------------------------------
const FlexItem = styled.div `height: 50%; width: 100%; margin: 5px 0px;
    display: inline-flex; flex-direction: column; justify-content: center; align-items: center;
    border-top: 2vh solid rgba(255,255,255,.4); border-bottom: 2vh solid rgba(255,255,255,.4);
border-radius: 10px; box-shadow: inset 0 0 200px 10px #000, inset 0 0 30px 10px #000; text-shadow: 0 8px 3px #000;
    @media (max-width: 600px)  { padding: 0px 10px }`
const Header  = styled.h2  `font-family: 'Architects Daughter', cursive; font-size: 300%; text-align: center; color: #fff; text-shadow: 0 1px 30px #fff;
    @media (max-width: 1200px) { font-size: 180% }
    @media (max-width: 700px)  { font-size: 150% }`
const Paragraph = styled.p `display: flex; justify-content: center; align-items: center; padding: 5px 10px;
 text-align: center; border-bottom: 1px solid #fff; font-family: 'Architects Daughter', cursive; text-shadow: 0 0 10px;
 font-size: 120%; color #fff; height: 80px;
    &:last-of-type { border: none; }
    @media (max-width: 1200px) { font-size: 120% }
    @media (max-width: 840px)  { font-size: 100%; }
    @media (max-width: 520px)  { padding: 4px; font-size: 90%; }
    @media (max-width: 420px)  { padding: 2px; font-size: 80%; }
     }
    `



export default Game